.top-navbar {
    position: fixed;
    background-color: white;
    width: 100vw;
  }
  
  .overlay {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(255, 255, 255, 0.6);
    pointer-events: none;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
  }
  
  .overlay > Button {
    pointer-events: all;
  }